
h1 {
  margin: 0 0 0 5px;
}

h2 {
  margin: 0;
}

p {
  margin: 0 0 5px 5px;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  align-items: start;

  width: 100%;
  height: 100%;
}

.wrapper {

}

.ranking-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.countries-card {
  display: flex;
  flex-direction: row;

  border-bottom: solid 1px gray;
  margin: 5px;
}

.scope-label {
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 30px;
  height: 30px;
  background: gray;
  border-radius: 5px;
  padding: 10px;
  color: white;
  margin: 10px;

  font-size: 23px;
}

.yellow-rank-number {
  position: absolute;

  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 50px;
  padding: 5px;
  top: -10px;
  right: -10px;

  height: 15px;
  width: 15px;

  background: #fdc62d;

  font-size: 15px;
}

.balance-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.kpi-card {
  display: flex;
}

.kpi-square {
  margin: 1px;

  width: 15px;
  height: 15px;

  border-radius: 3px;
}

.theme-yellow-top {
  background: rgb(255, 222, 0);
  background: linear-gradient(0deg, rgba(255, 222, 0, 1) 0%, rgba(253, 198, 45, 1) 100%);
}

.theme-yellow-bottom {
  background: #fdc62d;
}

.theme-red-top {
  background: rgb(255, 70, 70);
  background: linear-gradient(0deg, rgba(255, 70, 70, 1) 0%, rgba(251, 0, 0, 1) 100%);
}

.theme-red-bottom {
  background: #fb0000;
}

.theme-green-top {
  background: rgb(0, 255, 38);
  background: linear-gradient(0deg, rgba(0, 255, 38, 1) 0%, rgba(2, 210, 5, 1) 100%);
}

.theme-green-bottom {
  background: #02d205;
}